import * as React from 'react';
import { Wrapper } from '../src/components/Container';
import Home from '../src/models';

interface IProps {
    query: {
        id: string;
    }
}

export default class extends React.PureComponent<IProps> {
    static getInitialProps({ query }: any) {
        return { query }
    }

    render() {
        return (
            <Wrapper>
                <Home {...this.props} />
            </Wrapper>
        )
    }
}